.slider-input {
    width: 100%;
    margin-bottom: 15px;
  }
  
  .slider-container {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  
  .slider-value {
    min-width: 50px;
    text-align: center;
    font-weight: bold;
    background-color: #f5f5f5;
    padding: 5px 10px;
    border-radius: 4px;
  }
  
  .slider {
    flex: 1;
    -webkit-appearance: none;
    appearance: none;
    height: 10px;
    border-radius: 5px;
    background: #e0e0e0;
    outline: none;
  }
  
  .slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #b88339;
    cursor: pointer;
    transition: background 0.2s ease;
  }
  
  .slider::-moz-range-thumb {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #b88339;
    cursor: pointer;
    transition: background 0.2s ease;
  }
  
  .slider::-webkit-slider-thumb:hover {
    background: #97682a;
  }
  
  .slider::-moz-range-thumb:hover {
    background: #97682a;
  }
  
  /* Mobile styles */
  @media (max-width: 768px) {
    .slider {
      height: 15px; /* Slightly thicker for touch */
    }
    
    .slider::-webkit-slider-thumb {
      width: 30px;
      height: 30px;
    }
    
    .slider::-moz-range-thumb {
      width: 30px;
      height: 30px;
    }
  }