.game-container {
    background-color: #fff;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

/* Sample Card CSS */
.sample-card {
    background-color: #f8f5f0;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 16px;
    margin-bottom: 16px;
    text-align: left;
}

.sample-card h3 {
    font-family: 'Georgia', serif;
    color: #333;
}

.sample-card label {
    display: block;
    margin-bottom: 8px;
    font-family: 'Arial', sans-serif;
    color: #666;
}

.sample-card input,
.sample-card select {
    width: 100%;
    padding: 8px;
    margin-top: 4px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.sample-card button {
    background-color: #a0522d;
    color: #fff;
    border: none;
    padding: 10px;
    cursor: pointer;
    width: 100%;
    border-radius: 4px;
    margin-top: 16px;
}

.game-interface {
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
}

.game-interface h2 {
    font-family: 'Georgia', serif;
    color: #333;
    margin-bottom: 20px;
}

.submit-button {
    padding: 12px 24px;
    background-color: #228B22; /* Forest green for submit */
    color: white;
    border: none;
    border-radius: 5px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.submit-button:hover {
    background-color: #1b6b1b;
}

.game-interface {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    background-color: #faf7f2;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.sample-tabs {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: #faf7f2;
    padding: 10px 0;
    z-index: 10;
}

.sample-tab {
    flex: 1;
    padding: 10px;
    margin: 0 5px;
    background-color: #e8e3d8;
    border: 2px solid #d6cdb7;
    border-radius: 5px;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease;
}

.sample-tab.active {
    background-color: #c99c57;
    color: white;
    border-color: #b88339;
}

.sample-form {
    padding: 20px;
    background-color: white;
    border-radius: 8px;
    margin-bottom: 20px;
}

.form-group {
    margin-bottom: 25px;
}

.radio-group {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-top: 10px;
}

.radio-label {
    display: flex;
    align-items: center;
    padding: 8px 15px;
    background-color: #f5f5f5;
    border-radius: 20px;
    cursor: pointer;
    transition: background-color 0.2s ease;
}

.radio-label:hover {
    background-color: #e9e9e9;
}

.radio-label input {
    margin-right: 8px;
}

.flame-rating {
    display: flex;
    gap: 5px;
    margin-top: 10px;
}

.flame-icon {
    font-size: 24px;
    cursor: pointer;
    opacity: 0.3;
    transition: opacity 0.2s ease;
}

.flame-icon.active {
    opacity: 1;
}

.sample-navigation {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
}

.prev-button, .next-button {
    padding: 12px 24px;
    background-color: #b88339;
    color: white;
    border: none;
    border-radius: 5px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.prev-button:hover, .next-button:hover {
    background-color: #97682a;
}

.prev-button:disabled, .next-button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
}

/* Mobile responsive styles */
@media (max-width: 768px) {
    .sample-tabs {
        overflow-x: auto;
        padding-bottom: 10px;
    }

    .radio-group {
        flex-direction: column;
    }

    .sample-navigation {
        flex-direction: column;
        gap: 10px;
    }

    .prev-button, .next-button {
        width: 100%;
    }
}